@font-face {
    font-family: "coolvetica_rg";
    src: url("../fonts/coolvetica_rg-webfont.woff") format("woff2"),
      url("../fonts/coolvetica_rg-webfont.woff") format("woff");
    font-weight: normal;
    font-style: normal;
  }
  
  $font-primary: "coolvetica_rg";
  
  .home--button {
    width: calc(180px + 5%);
    height: 70px;
    margin: 0 auto;
    color: #000 !important;  
    padding: 0;

    a {
     
      padding: 10% 25%;
    }
    a:hover{
      color: #24687c ;
     
    }
  
    button {
      border: 3px solid #000 !important;
      width: 100%;
      height: 100%;
      color: #000 !important;
      font-size: 25px;
      font-family: $font-primary;
      text-transform: uppercase;
  
      font-weight: lighter;
      box-shadow: 5px 5px #000;
      cursor: pointer;
      letter-spacing: 5px;
      background-color: transparent;
    }
    button:hover {
      transform: scale(110%);
      color: #24687c !important;
      transition: transform 0.1s ease;
    }
  }
  
  @media (max-width: 480px) { 


    .home--button {
      width: calc(100px + 5%);
      height: 45px;
      margin: 0 auto;
      color: #000 !important;  
      padding: 0;

      button {

        font-size: 18px;
        letter-spacing: 2px;
        box-shadow: 3px 3px #000;

      
      }
     
      
    }
  }


  .modo-claro {
   
   .home--button  {
     
      margin-top: 5%;
    }
  }

  .modo-escuro {
    .home--button  {
     color: #24687c;
    }
  }